  flex-wrap: wrap;
  justify-content: space-around;
  width: 200px;
  margin: 0 auto;
  padding-top: 32px;
  padding-bottom: 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__product-name {
  padding-top: 40px;
  padding-bottom: 4px;
  display: flex;
}

.card__product-name .product-name {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  width: 200px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-self: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  width: 200px;
  padding-top: 24px;
  display: flex;
}

.card__price-word {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  text-align: right;
  color: #060b35;
