  line-height: 18px;
}

.card__button {
  padding-top: 16px;
}

.card .button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 0;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card .button:hover {
  color: #00acdc;
